.container{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    .title{
        margin-top: 2rem;
        font-weight: 600;
    }
    .carousel{
        .SliderCard{
            height: 100%;
            margin: 0 auto;
            overflow: hidden;
            position: relative;
            backdrop-filter: blur(16px) saturate(180%);
            -webkit-backdrop-filter: blur(16px) saturate(180%);
            background-color: rgba(17, 25, 40, 0.75);
            border: 1px solid rgba(255, 255, 255, 0.125);
            position: relative;
            .btn{
                scale: 0.5 !important;    
                }
            &:hover .description{
                display: flex;
                flex-direction: column;
                justify-content: center;
                color: var(--color-white);
                height: 100%;
                overflow: auto;
                // z-index: 5;
                overflow-y: scroll !important;
                -webkit-overflow-scrolling: touch;
                -webkit-overflow-scrolling: auto;
            }
            .description{
                position: relative;
                font-size: .7rem;
                background-color : rgba(17, 25, 40, 0.75);
                position: absolute;
                display: none;
                bottom: 0;
                left: 0;
                right: 0;
                // margin-bottom: 2rem;          
            }
            .name{
                margin-top: 1rem;
            }
            .img{
                width: 100%;
                height: 100%;
                color: red;
            }
    }
    }
}
.carAnim{
    animation: carAnim 5s ease forwards;
}

.buttons{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    flex-direction: row;
}

@keyframes carAnim {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}