.main{
    &.disable {
        opacity: 0;
        height: 0;
        z-index: -10;
    }
    &.diplayNone {
        display: none;
    }
    position: absolute;
    background-color: var(--color-bg);
    display: block;
    height:100%;
    z-index: 100;
    height: 10000rem;
    width: 100vw;
    img{
        width: 4rem;
        height: 4rem;
        position: fixed;
        margin: 0 auto;
        top: 45vh;
        z-index: 500;
        left: 45vw;
        animation: beat 1s ease-in-out alternate infinite;
    }
}

@keyframes beat{
    from{
        scale: 0.9;
    }
    to{
        scale: 1;
    }
}
@keyframes beatEnd{
    To{
        scale: 0%;
    }
}


@media screen and (max-width:1024px) {
    .main{
        img{
            scale: 5 !important;
        }
        }

}
@media screen and (max-width:600px) {
    .main{
        img{
            scale: 5 !important;
        }
        }

}