.container{
    .knowledges{
        margin-top: 2rem;
        width: 100%;
        margin-bottom: 2rem;
        &.fadeKno{
            animation: fadeKno 3s ease;
        }
        .knowledges__container{
            &.open{
                animation: openAnimation 1.5s ease;
            }
            font-weight: 600;
            margin: 0 auto;
            width: 80%;
            background-color: rgb(132, 125, 125,0.1);
            border-radius: .2rem;
            height: 2rem;
            overflow: hidden;
            .technologies{
                display: flex;
                flex-wrap: wrap;
                justify-content: space-around;
                align-items: center;
                border-top: 2px solid white;
                .techImage{
                    width: 5rem;
                }
            }
            .head{
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding-right: 1rem;
                padding-left: 1rem;
            
            }
            &.open{
                overflow: visible;
                height: max-content;
                padding-bottom: 1rem;
            }
            &:hover{
                cursor: pointer;
            }
            .logo{
                margin-top: .2rem;
                scale: 1.5;
                height: max-content;
                color: var(--color-white);
            }
        }
    }
}
@keyframes openAnimation{
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}
@keyframes fadeKno{
    from{
        // transform: translateY(10rem);
        opacity: 0;
        // display: none;
    }
    to{
        opacity: 1;
        // display: block;
        // transform: translateY(-3rem);
    }
}